body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  padding: 10px 20px;
}

.comments-text {
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  margin: 0;
  padding: 10px 20px; }

.comments-input {
  height: 30px;
  font-size: 16px;
  width: 100%;
  border: 1px solid #000;
  box-sizing: border-box; }

button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: transparent;
  border: none; }

.page_curtain_open {
  position: fixed;
  height: 100%; }

.news-comments-curtain {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: calc(100% - 10px);
  transition: -webkit-transform 1s cubic-bezier(0.42, 0, 1, 1);
  transition: transform 1s cubic-bezier(0.42, 0, 1, 1);
  transition: transform 1s cubic-bezier(0.42, 0, 1, 1), -webkit-transform 1s cubic-bezier(0.42, 0, 1, 1);
  -webkit-transform: translateY(100%);
          transform: translateY(100%); }
  .news-comments-curtain_mode_hidden {
    -webkit-transform: translateY(100%);
            transform: translateY(100%); }
  .news-comments-curtain_mode_folded {
    -webkit-transform: translateY(calc(100% - 52px));
            transform: translateY(calc(100% - 52px));
    box-shadow: 0 -16px 24px rgba(34, 36, 38, 0.1); }
  .news-comments-curtain_mode_open {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  .news-comments-curtain-container {
    position: relative;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: #fff; }
    .news-comments-curtain_mode_open .news-comments-curtain-container {
      border-top-left-radius: 15px;
      border-top-right-radius: 15px; }
  .news-comments-curtain-overlay {
    position: absolute;
    left: 0;
    top: calc(-100%);
    right: 0;
    bottom: 0;
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    transition: background-color 1s, -webkit-transform 0s 1s;
    transition: background-color 1s, transform 0s 1s;
    transition: background-color 1s, transform 0s 1s, -webkit-transform 0s 1s; }
    .news-comments-curtain_mode_open .news-comments-curtain-overlay {
      background-color: rgba(0, 0, 0, 0.2);
      -webkit-transform: translateY(0);
              transform: translateY(0);
      transition: background-color 1s; }
  .news-comments-curtain-header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 52px;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    position: absolute;
    box-sizing: border-box;
    width: 100%;
    padding: 0 12px 0 16px;
    visibility: hidden;
    background-color: #fff;
    z-index: 1; }
    .news-comments-curtain_mode_folded .news-comments-curtain-header {
      visibility: visible;
      transition: visibility 0s 1s; }
  .news-comments-curtain-fold {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 36px;
    -webkit-flex-shrink: 0;
            flex-shrink: 0; }
  .news-comments-curtain-write {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    padding: 0 12px;
    font-size: 16px;
    line-height: 36px;
    text-align: left;
    color: #939cb0;
    border-radius: 18px;
    background-color: rgba(18, 27, 73, 0.05); }
  .news-comments-curtain-save, .news-comments-curtain-view, .news-comments-curtain-like {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-align-self: stretch;
            align-self: stretch;
    min-width: 36px;
    padding: 0 4px;
    font-size: 14px;
    color: #939cb0; }
    .news-comments-curtain-save svg, .news-comments-curtain-view svg, .news-comments-curtain-like svg {
      margin: 0 4px; }
  .news-comments-curtain__save.turbo-button {
    margin-left: 4px; }
  .news-comments-curtain-content {
    overflow: auto;
    -webkit-overflow-scrolling: touch; }

